/* 공통 폰트 스타일 적용 */
.highlight code{
    color: #f8f8f2;
    background-color: #1b1d1e;
    font-family: "JetBrains Mono", "Noto Sans KR", serif;
  }
.highlight table td { padding: 5px; }
.highlight table pre { margin: 0; }
